.container {
    margin: 0% 2.5%;
}

img {
    max-width: 100%;
    max-height: 100%;
}

.card-image {
    max-width: 100%;
    max-height: 100%;
}

@media only screen and (max-width: 750px) {
    .products-grid {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 5px;
        grid-template-rows: 180px;
    }
}

@media only screen and (max-width: 1180px) and (min-width: 751px) {
    .products-grid {
        grid-template-columns: 1fr 1fr;
    }
}
